body {
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 800;
}

:root {
  --icon-big: url("Search.ccd3c07b.svg") 26px 50% no-repeat;
  --icon-small: url("Search.ccd3c07b.svg") 13px 50% no-repeat;
}

.search-bar {
  margin-top: 20px;
}

.search-bar__input {
  box-sizing: border-box;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

.big {
  background: var(--icon-big);
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.small {
  background: var(--icon-small);
  background-size: 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-bar__input::placeholder {
  color: #3d4e61;
  font-family: inherit;
  font-weight: 300;
}

.small::placeholder {
  font-size: 14px;
}

.big:hover {
  box-shadow: 0 4px 4px #00000040;
}

.small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input:focus {
  border-radius: 3px;
  outline: none;
  font-weight: 800;
}

.big:focus {
  text-shadow: 0 4px 4px #00000040;
  background: var(--icon-big), linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

.small:focus {
  background: var(--icon-small), linear-gradient(180deg, #fff, #f6f6f7);
  box-shadow: none;
  background-size: 11px;
}
